import React from 'react';

import { 
    Container,
    Card,
    useStyles,
    ViewImagem, 
} from './styles';

import SaveIcon from '@material-ui/icons/Save';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AddIcon from '@material-ui/icons/Add';

import { AdicionarGaleria } from './Adicionar';
import { EditarGaleria } from './Editar';
import { Carousel } from './Carousel';
import Modal from '@material-ui/core/Modal';

import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';

import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import Typography from '@material-ui/core/Typography';

import { APIURL } from '../../environment';

import withWidth, { isWidthUp } from '@material-ui/core/withWidth';


const Galeria = (props) => {
    
    const theme = useTheme();
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [carousel, setCarousel] = React.useState({ status: false, index: 0});

    const [openALert, closeAlert, Alert] = useSnackbarsAlert()
    const [openBackdro, closeBackdro, Backdro] = useBackdrop()

    const [galeria, setGaleria, GaleriaInicial, addGaleria, rmGaleria, atuGaleria, upGaleria] = useGenericModel('account_galery');
    
    const handleClose = (update=false) => {
        setOpen(false);
        if(update){        
          setTimeout(() => {
              upGaleria()
          }, 700);
        }
    };

    React.useEffect( ()=>{
    },[galeria])

    const setIndexCarousel = (index) => {
      setCarousel({ status: true, index: index})
    }

    return (
        <>
        <Container>
                {
                     galeria.imagens?.length > 0 ?
                        galeria.imagens.map((item, index) => (
                          <GaleriaItem galeriaItem={item} 
                                        index={index} 
                                        carousel={setIndexCarousel} 
                                        Backdro={[openBackdro, closeBackdro]} 
                                        openALert={openALert} 
                                        upGaleria={upGaleria}
                                        />
                        ))  
                     :
                     <></>
                }
        </Container>
        <Carousel carousel={carousel} close={ () => setCarousel({ status: false, index: 0}) } imagesList={ galeria?.imagens ? galeria.imagens : []} setIndexCarousel={setIndexCarousel} />

            <Modal
                    open={open}
                    onClose={handleClose}
                    className={classes.modal}
                    aria-labelledby="simple-modal-galeria"
                    aria-describedby="simple-modal-galeria"
                >
                <AdicionarGaleria close={handleClose} Backdro={[openBackdro, closeBackdro]} openALert={openALert} />
            </Modal>

            <Zoom
              key="primary"
              in={1}
              timeout={{
                enter: theme.transitions.duration.enteringScreen,
                exit: theme.transitions.duration.leavingScreen,
              }}
              style={{
                transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
              }}
              unmountOnExit
            >
              <Fab 
                onClick={ () => setOpen(true) }
                aria-label="addicon"
                color="primary"
                style={{
                  position: 'fixed',
                  bottom: '30px',
                  right: '30px',
                  backgroundImage: 'linear-gradient(to right, #8f00dd , #6866ff)',
                }}
              >
                <AddIcon />
              </Fab>
          </Zoom>
        <Alert />
        <Backdro />
        </>
    );
}

const GaleriaItem = ({galeriaItem, index, carousel, Backdro, openALert, upGaleria}) => {

  const [open, setOpen] = React.useState(false);
  const classes = useStyles();

  const handleOpen = () => {
      setOpen(true);
    };
    
    const handleClose = (update=false) => {
      setOpen(false);
      if(update){        
        setTimeout(() => {
            upGaleria()
        }, 700);
      }
  };

  return (
    <>
      <Card >
          <img src={`${APIURL}${galeriaItem?.img}`} alt={galeriaItem.nome}
                style={{
                  cursor: 'pointer'
                }}  
                onClick={() => carousel(index) }
          />
            <div className="info">
                <div className="text">
                <Typography variant="overline" display="block" gutterBottom>
                    {galeriaItem?.nome}
                </Typography>
                <Typography variant="caption" display="block" gutterBottom>
                    {galeriaItem?.descricao}
                </Typography>
                </div>
                <div className="icon">
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="start"
                    onClick={handleOpen}
                >
                    <InfoIcon style={{color: '#ffffff'}} />
                </IconButton>
                </div>
            </div>
      </Card>
      <Modal
        open={open}
        onClose={handleClose}
        className={classes.modal}
        aria-labelledby="simple-modal-galeria"
        aria-describedby="imple-modal-galeria"
      >
        <EditarGaleria galeriaItem={galeriaItem} close={handleClose} Backdro={Backdro} openALert={openALert} />
      </Modal>       
    </>
  )
}


export default withWidth()(Galeria);
