import React from 'react';
import { AdicionarServico } from './Adicionar';
import { EditarServico } from './Editar';
import {
    Container,
    WrapperHeader,
    MenuFiltro,
    WrapperAdd,
    WrapperLista,
    GrupoLista,
    GrupoTitulo,
    GrupoItens,
    CardServico,
    WrapperCardMedia,
    useStyles,
} from './styles';

import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';

import Divider from '@material-ui/core/Divider';

// import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import Modal from '@material-ui/core/Modal';

import {
    Link
  } from "react-router-dom";

  import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';

import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';

import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';

import { AlertDialog } from '../../components';

import { remove } from '../../utils/Services';

const Servico = () => {

    const [categorias, setCategoria, CategoriasInicial, addCategoria, rmCategoria, putCategoria, upCategorias] = useGenericModel('account_services_category');
    const [selectCategoria, setSelectCategoria] = React.useState('all');
    const [categoriaLista, setCategoriaLista] = React.useState([]);
    const [open, setOpen] = React.useState(false);

    const [openALert, closeAlert, Alert] = useSnackbarsAlert()
    const [openBackdro, closeBackdro, Backdro] = useBackdrop()

    const handleChange = (event) => {
        setSelectCategoria(event.target.value);
        if(event.target.value === 'all') {
            setCategoriaLista(categorias)
        }else{
            setCategoriaLista([categorias[event.target.value]])
        }
    };

    const handleOpen = () => {
        setOpen(true);
      };
    
    const handleClose = () => {
        setOpen(false);
        upCategorias();
    };

    React.useEffect(()=>{
        setCategoriaLista(categorias)
    },[categorias])

    const classes = useStyles();
    const theme = useTheme();

    return (
        <Container >
            <WrapperHeader >

                <MenuFiltro >
                    <FormControl className={classes.formControl}>
                        <InputLabel 
                            style={{ 
                                margin: '3px',
                                fontSize: '14px',
                            }}
                        >
                            Categoria
                        </InputLabel>
                        <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={selectCategoria}
                        style={{ 
                            width: '100%'
                        }}
                        variant="outlined"
                        onChange={handleChange}
                        InputLabelProps={{
                            shrink: true,
                        }}
                        >
                        {
                            categorias.filter( (categoria, index) => ( categoria?.servico.length > 0 ? true : false )).length > 0 ?  <MenuItem value={'all'}>Todas</MenuItem> : false
                        }
                        {
                            categorias.map( (categoria, index) => {
                                return categoria?.servico.length > 0 ? <MenuItem value={index}>{categoria.name}</MenuItem> : false
                            })
                        }
                        </Select>
                    </FormControl>
                </MenuFiltro>

                {/* <WrapperAdd >
                    <Button variant="contained" color="primary" onClick={handleOpen} >
                        Adicionar
                    </Button>
                </WrapperAdd> */}

            </WrapperHeader>
        <WrapperLista>
            {
                categoriaLista.map( (categoria, index) => {
                    return categoria?.servico.length > 0 ? <CategoriaItem key={index} categoria={categoria} useSnackbarsAlert={{openALert, closeAlert}} useBackdrop={{openBackdro, closeBackdro}} upCategorias={upCategorias} /> : <></>
                })
            }
        </WrapperLista>
        <Modal
                open={open}
                onClose={handleClose}
                className={classes.modal}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
            <AdicionarServico handleClose={handleClose}  useSnackbarsAlert={{openALert, closeAlert}} useBackdrop={{openBackdro, closeBackdro}} />
        </Modal>

        <Zoom
            key="primary"
            in={1}
            timeout={{
              enter: theme.transitions.duration.enteringScreen,
              exit: theme.transitions.duration.leavingScreen,
            }}
            style={{
              transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
            }}
            unmountOnExit
          >
            <Fab 
              onClick={handleOpen}
              aria-label="addicon" 
              color="primary"
              style={{
                position: 'fixed',
                bottom: '30px',
                right: '30px',
                backgroundImage: 'linear-gradient(to right, #8f00dd , #6866ff)',
              }}
            >
              <AddIcon />
            </Fab>
          </Zoom>
        <Alert />
        <Backdro />
    </Container>
    );
}

const CategoriaItem = ({categoria, useSnackbarsAlert, useBackdrop, upCategorias}) => {
    
    
    return (
        <GrupoLista>
                <GrupoTitulo>
                    <Typography variant="h6">
                        {categoria.name}
                    </Typography>
                </GrupoTitulo>
                <GrupoItens>
                {
                categoria.servico.map((servico, index) => (
                        <ServicoItem servico={servico} useSnackbarsAlert={useSnackbarsAlert} useBackdrop={useBackdrop} upCategorias={upCategorias} />
                    ))
                }
                </GrupoItens>
        </GrupoLista>
    )

}

const ServicoItem = ({servico, useSnackbarsAlert, useBackdrop, upCategorias}) => {

    const classes = useStyles();

    const [open, setOpen] = React.useState(false);
    const [dialog, setDialog] = React.useState(false);

    const handleOpen = () => {
        setOpen(true);
    };
    
    const handleClose = (update=false) => {
        setOpen(false);
        if(update){
            upCategorias()
        }
    };

    const handleRemove = async () => {
        let result = await remove('account_services', `${servico.id}/`)
        if(result?.status){
            handleClose(true)
        }else{
            handleClose(false)
        }
    }

    return (
        <>
        <Card className={classes.card} >
            <CardContent className={classes.cardContent} >
                <WrapperCardMedia>
                    <CardMedia
                    className={classes.cover}
                    image={servico?.img ? servico?.img : ''}
                    title="Live from space album cover"
                    />
                </WrapperCardMedia>
                    <div>
                        <CardContent >
                            <Typography variant="h6">
                                {servico.nome}
                            </Typography>
                            <Typography variant="body2" color="textSecondary">
                                {servico.descricao}
                            </Typography>
                        </CardContent>
                    </div>
                </CardContent>
                <CardActions>
                            <Button size="small" onClick={handleOpen} >EDITAR</Button>
                            <Button size="small" onClick={() => setDialog(true)} >REMOVER</Button>
                </CardActions>
        </Card>
        <Modal
                open={open}
                onClose={handleClose}
                className={classes.modal}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
            <EditarServico handleClose={handleClose} servico={servico} useSnackbarsAlert={useSnackbarsAlert} useBackdrop={useBackdrop} />
        </Modal>
        <AlertDialog 
                dialog={dialog} 
                setDialog={setDialog} 
                titulo={'Alerta'} 
                handleCancelar={() => setDialog(false)} 
                handleConfirmar={handleRemove}>
        <Typography variant="overline" display="block" gutterBottom>
            Deseja confirmar a exclusão do serviço {servico.nome}
        </Typography>
        </AlertDialog> 
        </>
    )

}


export  { Servico };
